import React from 'react';
import Slider from 'react-slick';

import FeedbackForm from "./FeedbackForm";
import AboutUs from "./AboutUs"


export default class SliderMain extends React.Component {
    constructor() {
        super();

        this.state = {
            showModal: false
        };
        this.open = this.open.bind(this);
        this.close = this.close.bind(this);
    }
    close() {
        this.setState({ showModal: false });
    }
    open() {
        this.setState({ showModal: true });
    }
    render () {
        console.log("slider");
        let settings = {
            dots: true,
            infinite: true,
            slidesToShow: 1,
            slidesToScroll: 1,
            autoplay:true,
            touchMove:true,
            speed:2000,
            cssEase: 'ease-in',
            autoplaySpeed:6000
        };
        return (
            <section className="slider-for-large">
            <Slider  className="slider-main-page" ref={c => this.slider = c } {...settings}>
                <div className="slider slider1">
                    <article className="post slider1">
                        <div className="container-img-slider">
                           <img className="slider-img" src={require("../../assets/img/WhatisCityElf.png")}/>
                        </div>
                            <div className="post__content slide-about-us">
                                    <h1 className="post__header">  ityElf?</h1>
                                    <p className="post__text slide1"> <span className="our_name">CityElf </span>    :   .</p>
                                    <p className="post__text slide1">       , ,      ,   .</p>
                                    <p className="post__text slide1">          <span className="our_name"> CityElf </span>    !</p>
                                    <p className="post__text slide1">        .</p>
                                    <p className="post__text slide1">         ,     .</p>
                                    <p className="post__text ">      ,     .</p>
                            </div>
                    </article>
                </div>
                <div className="slider our-team">
                    <article className="post slider2">
                        <div className="container-img-slider">
                            <img className="slider-img team-img" src={require("../../assets/img/team.png")}/>
                        </div>
                            <div className="post__content">
                                <h2 className="post__header"> </h2>
                                <p className="post__text"> <span className="our_name">CityElf </span>     .        .</p>
                                <p className="post__text">      ,      <span className="our_name">CityElf </span> .</p>
                                <p className="post__text">      -      .</p>
                            </div>
                    </article>
                </div>
                <div className="slider">
                    <article className="post slider1">
                        <div className="container-img-slider">
                            <img className="slider-img" src={require("../../assets/img/social-network-people.png")}/>
                        </div>
                            <div className="post__content">
                                    <h2 className="post__header">  </h2>
                                <ul className="feedback__text">
                                    <li className="post__text_list">        ?</li>
                                    <li className="post__text_list">        ?</li>
                                    <li className="post__text_list">   ?</li>
                                </ul>
                                <p className="post__text contact-us"> !</p>
                                <button onClick={this.open} type="submit" className="btn send-info-about-us" id="writeusbtn"> </button>
                            </div>
                    </article>
                    <FeedbackForm showModal = {this.state.showModal} closeModal = {this.close} feedBackRequest={this.props.feedBackRequest}/>
                </div>
            </Slider>
            </section>

        );
    }
}
SliderMain.propTypes = {
    feedBackRequest: React.PropTypes.func.isRequired
}



